<template>
<nav-bar>
  <span slot="title" class="title">优惠劵管理</span>
  <a slot="right-content" class="nav-link btn btn-primary-outline btn-sm pull-xs-right" @click="showForm">
    <i class="fa fa-plus"></i> 添加
  </a>
</nav-bar>
<table class="table">
  <thead>
    <tr>
      <td>主题</td>
      <td>内容</td>
      <td>原价格</td>
      <td>优惠价格</td>
      <td>操作</td>
    </tr>
  </thead>
  <tbody>
    <tr v-for="coupon in coupons">
      <td>{{coupon.title}}</td>
      <td>{{coupon.details}}</td>
      <td>{{coupon.original_price}}</td>
      <td>{{coupon.preferential_price}}</td>
      <td>
        <a v-link="'/coupon/'+coupon.id" class="btn raised btn-primary btn-sm">修改</a>
        <button @click="remove(coupon)" class="btn btn-danger btn-sm">删除</button>
      </td>
    </tr>
  </tbody>
</table>
<new-object :show.sync="showModal">
  <span slot="header">创建新优惠劵</span>
  <div slot="body">
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-3 form-control-label">主题</label>
      <div class="col-sm-9">
        <input class="form-control" placeholder="主题" type="text" v-model="newCoupon.title" />
      </div>
    </div>
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-3 form-control-label">详情</label>
      <div class="col-sm-9">
        <textarea class="form-control" rows="3" v-model="newCoupon.details" placeholder="详情"></textarea>
      </div>
    </div>
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-3 form-control-label">原价格</label>
      <div class="col-sm-9">
        <input class="form-control" placeholder="奖品数量" type="text" v-model="newCoupon.original_price" />
      </div>
    </div>
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-3 form-control-label">优惠价格</label>
      <div class="col-sm-9">
        <input class="form-control" placeholder="奖品数量" type="text" v-model="newCoupon.preferential_price" />
      </div>
    </div>
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-3 form-control-label"></label>
      <div class="col-sm-9">
        <button @click="create" class="btn btn-primary">保 存</button>
      </div>
    </div>
  </div>
</new-object>
</template>
<script>
  import { showAlert, showLoading, hideLoading } from '../vuex/actions'

  export default {
    vuex: {
      getters: {
        isLogged: state => state.merchant.isLogged,
        access_token: state => state.merchant.merchant.access_token
      },
      actions: {
        showAlert,
        showLoading,
        hideLoading
      }
    },
    data () {
      return {
        showModal: false,
        coupons: [],
        newCoupon: {
          title: '',
          details: '',
          original_price: 0,
          preferential_price: 0
        }
      }
    },
    route: {
      data (transition) {
        this.showLoading()
        this.$http.get('http://jiancan.me/api/m1/shops/coupons.json', { access_token: this.access_token }).then(function (response) {
          transition.next({
            coupons: response.data
          })
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    methods: {
      showForm () {
        this.$set('showModal', true)
      },
      create () {
        this.showLoading()
        this.$http.post('http://jiancan.me/api/m1/coupons.json', { access_token: this.access_token, coupon: this.newCoupon }).then(function (response) {
          this.coupons.push(response.data)
          this.newCoupon = { }
          this.showModal = false
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      },
      remove (coupon) {
        this.showLoading()
        this.coupons.$remove(coupon)
        this.$http.delete('http://jiancan.me/api/m1/coupons/one.json', { access_token: this.access_token, coupon_id: coupon.id }).then(function (response) {
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue'),
      'new-object': require('../components/modal-mask/new-object.vue')
    }
  }
</script>



